










































import Vue, { PropOptions } from "vue";
import { EKontoModule, EKontoModuleData, EKontoModuleOptions } from "@exp/konto-module";
import { FModal, FForm, FIcon } from "@fkui/vue";

export default Vue.extend({
    name: "KontoWizardModule",
    components: { EKontoModule, FModal, FForm, FIcon },
    model: {
        event: "change",
        prop: "value",
    },
    props: {
        value: {
            required: true,
            type: Object,
        } as PropOptions<EKontoModuleData>,
    },
    data() {
        return {
            data: new EKontoModuleData({ ...this.value }),
            options: new EKontoModuleOptions(false),
        };
    },
    watch: {
        value: function () {
            this.data = new EKontoModuleData({ ...this.value });
        },
    },
    methods: {
        onSubmit(): void {
            this.options.isEdit = false;
            this.$emit("change", this.data);
        },
        onAbort(): void {
            this.data = new EKontoModuleData({ ...this.value });
            this.options.isEdit = false;
        },
    },
});
