import Router from "vue-router";
import Forsteg from "@/views/Forsteg.vue";
import Ansokan from "@/views/Ansokan.vue";
import Granska from "@/views/Granska.vue";
import Finalize from "@/views/Finalize.vue";
import Kvittens from "@/views/Kvittens.vue";
import TjanstEjTillganglig from "@/views/TjanstEjTillganglig.vue";
import { AnsokanService } from "@/services";
import { withConfirmNavigation } from "@exp/vue";
import VueRouter from "vue-router";

export default new Router({
    routes: [
        {
            path: "/",
            name: "Försteg",
            component: Forsteg,
            props: () => ({
                appModel: AnsokanService.getModel(),
            }),
            beforeEnter: (to, from, next) => {
                const model = AnsokanService.getModel();
                // Inga hundar finns, skickar användaren till tjänst ej tillgänglig.
                if (model.barn.length === 0) {
                    next({ name: "TjanstEjTillganglig" });
                } else if (model.barn.length === 1) {
                    // Om bara en hund finns, sätt valdHund och skicka användaren till ansökan.
                    model.valtBarn = model.barn[0];
                    next({ name: "Ansökan" });
                } else {
                    next();
                }
            },
        },
        {
            path: "/ansokan",
            name: "Ansökan",
            component: withConfirmNavigation(Ansokan, ["Försteg"]),
            props: () => ({
                valtBarn: AnsokanService.getModel().valtBarn,
            }),
            beforeEnter: (to, from, next) => {
                const { valtBarn } = AnsokanService.getModel();
                if (valtBarn.fornamn) {
                    next();
                } else {
                    next({ name: "Försteg" });
                }
            },
        },
        {
            path: "/finalize",
            name: "Finalize",
            component: Finalize,
        },
        {
            path: "/granska",
            name: "Granska",
            component: Granska,
        },
        {
            path: "/kvittens",
            name: "Kvittens",
            component: Kvittens,
        },
        {
            path: "/ejtillganglig",
            name: "TjanstEjTillganglig",
            component: TjanstEjTillganglig,
        },
        {
            // default route, redirect to Forsteg
            path: "*",
            redirect: "/",
        },
    ],
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition;
        } else {
            return { x: 0, y: 0 };
        }
    },
});
