














































import Vue, { PropOptions } from "vue";
import { FRadioGroup, FRadioGroupField } from "@fkui/vue";
import { Fragor } from "@/services/ApplicationModel/ApplicationModelInterface";

export default Vue.extend({
    components: { FRadioGroup, FRadioGroupField },
    model: {
        prop: "model",
        event: "change",
    },
    props: {
        model: {
            type: Object,
            required: true,
        } as PropOptions<Fragor>,
    },
    data() {
        return {
            fragor: { ...this.model },
        };
    },
    methods: {
        onChange(): void {
            this.$emit("change", this.fragor);
        },
    },
});
