




































































import Vue, { PropOptions } from "vue";
import { FForm, FRadioGroup, FRadioGroupField, FAnimateExpand } from "@fkui/vue";
import { ApplicationModel, BarnModel } from "@/services";
import { BarnValjare } from "@/components/forsteg";

export default Vue.extend({
    name: "Forsteg",
    components: {
        FForm,
        BarnValjare,
        FRadioGroup,
        FRadioGroupField,
        FAnimateExpand,
    },
    props: {
        appModel: {
            type: Object,
            required: true,
        } as PropOptions<ApplicationModel>,
    },
    data() {
        return {
            valtBarn: {} as BarnModel,
            anledning: "",
        };
    },
    computed: {
        isBarnSelected(): boolean {
            return this.valtBarn.fornamn !== undefined;
        },
    },
    methods: {
        onsubmit(): void {
            // eslint-disable-next-line vue/no-mutating-props
            this.appModel.valtBarn = this.valtBarn;
            // eslint-disable-next-line vue/no-mutating-props
            this.appModel.anledning = this.anledning;
            this.$router.push({
                name: "Ansökan",
            });
        },
        onNavigate(): void {
            alert("Tjänsten finns inte än!");
        },
    },
});
