



































































import Vue, { PropOptions } from "vue";
import { EAbortModal, unregisterConfirmNavigation } from "@exp/vue";
import { FLoader, FWizard, FWizardStep, FIcon, BeforeNextAction } from "@fkui/vue";
import { ValidationService } from "@fkui/logic";
import { AnsokanService, BarnModel } from "@/services";
import { BadRequestError } from "@exp/logic";
import { FelmeddelandeRestVO } from "hund-api/dist/typescript-axios";
import { SjukdomOchSymptom, DagarMedVab, Barnomsorg, FragorViMasteStalla } from "@/components/ansokan";

export default Vue.extend({
    name: "Ansokan",
    components: {
        EAbortModal,
        FWizard,
        FWizardStep,
        FLoader,
        FIcon,
        SjukdomOchSymptom,
        DagarMedVab,
        Barnomsorg,
        FragorViMasteStalla,
    },
    props: {
        valtBarn: {
            type: Object,
            required: true,
        } as PropOptions<BarnModel>,
    },
    data() {
        return {
            ansokan: AnsokanService.getModel(),
            currentStep: this.$router.currentRoute.query.step || "step1",
            hasBeenOpened: false,
            showAbortModal: false,
            isLoading: false, // Setting to false temporarily to allow running flow without being logged in. Need better solution.
            inFlight: false,
            serverErrors: new Array<FelmeddelandeRestVO>(),
            isDagarMedVabSubmitted: false,
        };
    },
    computed: {
        isDirty(): boolean {
            return ValidationService.isAnyTouched;
        },
        showDagarMedVabSaknas(): boolean {
            return this.isDagarMedVabSubmitted && Object.keys(this.ansokan.dagarMedVab).length === 0;
        },
    },
    beforeMount() {
        ValidationService.clearAllStates();
    },
    methods: {
        onClickGranska(): void {
            this.inFlight = true;
            window.setTimeout(() => {
                this.$router.push({
                    name: "Granska",
                });
            }, 1000);
        },

        onWizardCancel(): void {
            alert("wizard cancel");
        },
        onAbortConfirm(): void {
            unregisterConfirmNavigation();
            AnsokanService.abort();
        },
        onAbortClick(): void {
            /* if the form is dirty we need to confirm before aborting */
            if (this.isDirty) {
                this.showAbortModal = true;
            } else {
                AnsokanService.abort();
            }
        },
        onAbortDismiss(): void {
            this.showAbortModal = false;
        },
        async onsubmit(): Promise<void> {
            if (this.inFlight) {
                return;
            }
            this.inFlight = true;
            AnsokanService.saveModelToSessionStorage();
            try {
                await AnsokanService.submit();
            } catch (error) {
                switch (error.constructor) {
                    case BadRequestError:
                        this.handleBadRequestError(error);
                        break;
                    default:
                        throw error;
                }
            }
        },
        handleBadRequestError(error: BadRequestError<FelmeddelandeRestVO>) {
            this.inFlight = false;
            AnsokanService.handleServerValidation(error.data, this.$t);
            this.serverErrors = error.data;
        },
        async onLoadingFinished(elementId: string) {
            this.isLoading = false;
            await this.$nextTick();
            ValidationService.validateAllElements(elementId);
        },
        async dagarMedVabBeforeNext(): Promise<BeforeNextAction> {
            this.isDagarMedVabSubmitted = true;

            const hasSelectedDays = Object.keys(this.ansokan.dagarMedVab).length > 0;
            if (hasSelectedDays) {
                return BeforeNextAction.CONTINUE;
            } else {
                await this.$nextTick();
                (this.$refs.dagarMedVabSaknas as HTMLElement).focus();

                return BeforeNextAction.CANCEL;
            }
        },
    },
});
