












































































































import Vue, { PropOptions } from "vue";
import {
    FRadioGroup,
    FRadioGroupField,
    FIcon,
    FModal,
    FTextField,
    FPostalCodeTextField,
    FValidationGroup,
    GroupValidityEvent,
} from "@fkui/vue";
import { ValidationService } from "@fkui/logic";
import { BarnModel } from "@/services";
import { Barnomsorg } from "@/services/ApplicationModel/ApplicationModelInterface";

export default Vue.extend({
    components: {
        FRadioGroup,
        FRadioGroupField,
        FIcon,
        FModal,
        FTextField,
        FPostalCodeTextField,
        FValidationGroup,
    },
    model: {
        prop: "model",
        event: "change",
    },
    props: {
        model: {
            type: Object,
            required: true,
        } as PropOptions<Barnomsorg>,
        valtBarn: {
            type: Object,
            required: true,
        } as PropOptions<BarnModel>,
    },
    data() {
        return {
            barnomsorg: { ...this.model },
            isModalOpen: false,
            barnomsorgModal: { ...this.model },
            modalValidity: {} as GroupValidityEvent,
        };
    },
    methods: {
        onChange(): void {
            this.$emit("change", this.barnomsorg);
        },
        async onClickEdit(): Promise<void> {
            this.barnomsorgModal = { ...this.barnomsorg };
            this.isModalOpen = true;
            window.setTimeout(() => {
                ValidationService.setState("modalen", { touched: false, submitted: false });
                ValidationService.clearAllStates();
                ValidationService.validateAllElements("modalen");
            }, 1000);
        },
        onClickSaveModal(): void {
            if (this.modalValidity.isValid) {
                this.barnomsorg = { ...this.barnomsorgModal };
                this.onClickCloseModal();
            }
        },
        onClickCloseModal(): void {
            this.isModalOpen = false;
        },
    },
});
