import { AnsokanServiceInterface } from "./AnsokanServiceInterface";
import { APP_ENTRY_POINT_URL } from "@/constants";
import {
    fromRESTtoBarnModel,
    fromModelToAnsokanREST,
    fromRESTtoKvittensModel,
} from "@/services/TransformService";
import {
    ApplicationModel,
    ApplicationModelImpl,
    KvittensModel,
} from "@/services/ApplicationModel";
import { RestService } from "@/repository";
import { EKontaktModuleData } from "@exp/kontakt-module";
import { EKontoModuleData } from "@exp/konto-module";
import { ESgiModuleData } from "@exp/sgi-module";
import { EArbetsgivareModuleData } from "@exp/arbetsgivare-module";
import { SimplePersistenceService } from "@fkui/logic";
import { ValidationService, TranslateFunction } from "@fkui/logic";
import { unregisterConfirmNavigation } from "@exp/vue";
import { FelmeddelandeRestVO } from "hund-api/dist/typescript-axios";

const SERVER_VALIDATION_ERROR = "102";

export class AnsokanServiceImpl implements AnsokanServiceInterface {
    private MODEL_SESSION_KEY = "fk-hund-model";
    private KVITTENS_SESSION_KEY = "fk-hund-kvittens";
    private model: ApplicationModelImpl;
    private applicationStorage: SimplePersistenceService<ApplicationModelImpl>;
    private kvittensModel?: KvittensModel;
    private kvittensStorage: SimplePersistenceService<KvittensModel>;

    public constructor() {
        this.model = new ApplicationModelImpl();
        this.kvittensStorage = new SimplePersistenceService<KvittensModel>(
            this.KVITTENS_SESSION_KEY,
        );
        this.applicationStorage = new SimplePersistenceService<
            ApplicationModelImpl
        >(this.MODEL_SESSION_KEY);
    }

    public abort(): void {
        this.removeModelFromSessionStorage();
        window.location.href = APP_ENTRY_POINT_URL;
    }

    /**
     * Post ansökan to REST-service.
     */
    public async submit(): Promise<void> {
        const ansokan = fromModelToAnsokanREST(this.model);
        const signera = await RestService.postAnsokan(ansokan);
        unregisterConfirmNavigation();
        window.location.href = signera.redirectUrl;
    }

    /**
     * Finalize signing.
     */
    public async finalizeSigning(): Promise<void> {
        const response = await RestService.postSignera();
        this.kvittensModel = fromRESTtoKvittensModel(response);
        this.kvittensStorage.set(this.kvittensModel);
        this.removeModelFromSessionStorage();
    }

    public getModel(): ApplicationModel {
        return this.model;
    }

    public async initialize(): Promise<void> {
        const result = await RestService.getBarn();
        this.model.barn = fromRESTtoBarnModel(result);
    }

    public setKontaktData(kontaktData: EKontaktModuleData): void {
        this.model.kontaktData = kontaktData;
    }

    public setKontoData(kontoData: EKontoModuleData): void {
        this.model.kontoData = kontoData;
    }

    public setSgiData(sgiData: ESgiModuleData): void {
        this.model.sgiData = sgiData;
    }

    public setArbetsgivarData(arbetsgivarData: EArbetsgivareModuleData): void {
        this.model.arbetsgivarData = arbetsgivarData;
    }

    public saveModelToSessionStorage(): void {
        this.applicationStorage.set(this.model);
    }

    public removeModelFromSessionStorage(): void {
        this.applicationStorage.remove();
    }

    public fetchModelFromSessionStorage(): void {
        this.model = this.applicationStorage.get(this.model);
    }

    public getKvittensModel(): KvittensModel {
        if (this.kvittensModel) {
            return this.kvittensModel;
        }
        return this.kvittensStorage.get();
    }

    public handleServerValidation(
        data: FelmeddelandeRestVO[],
        $t: TranslateFunction,
    ): void {
        for (const error of data) {
            if (error.kod === SERVER_VALIDATION_ERROR && error.parameternamn) {
                const elementId = this.getElementId(
                    error.parameternamn,
                    error.meddelande,
                );
                ValidationService.setState(elementId, {
                    serverError: $t(error.textnyckel ? error.textnyckel : ""),
                });
            }
        }
        ValidationService.validateAllElements("hundbidraget-form");
    }

    private getElementId(parameternamn: string, meddelande?: string): string {
        const element = document.getElementById(parameternamn);
        if (!element) {
            throw new Error(
                `Failed to map server validation error "${meddelande}" to field "${parameternamn}"`,
            );
        }
        return parameternamn;
    }
}
// Export a single instance of the service (singleton pattern).
export const AnsokanService = new AnsokanServiceImpl();
