

































import Vue from "vue";
import { FIcon } from "@fkui/vue";

export default Vue.extend({
    name: "App",
    components: { FIcon },
    data() {
        return { demoBanner: true };
    },
    created() {
        this.demoBanner = window.sessionStorage.getItem("demobanner") !== "off";
    },
    mounted() {
        (document.getElementById("f-offline-component") as HTMLElement).appendChild(
            this.$refs["banner"] as HTMLElement,
        );
    },
    methods: {
        hideBanner(): void {
            window.sessionStorage.setItem("demobanner", "off");
            this.demoBanner = false;
        },
    },
});
