import { ValidationService, ValidatorName, ValidationErrorMessageBuilder } from "@fkui/logic";
import {
    FFormStep,
    FFormStepButton,
    FSelectField,
    FRadioGroup,
    FRadioGroupField,
    FTextareaField,
    FTextField,
    FTooltip,
} from "@fkui/vue";
import { EFileUpload, UploadError } from "@exp/vue";
import FkRichtext from "fk-vue-richtext";

ValidationService.registerValidator({
    name: "validId" as ValidatorName,
    validation(value) {
        const HUNDID_REGEXP = /^\d{5}[MNPSTVWXYZ]{1}$/;
        return HUNDID_REGEXP.test(value);
    },
});

ValidationService.addValidationErrorMessages(
    ValidationErrorMessageBuilder.create()
        .map("validId" as ValidatorName, "ID numret följer inte Svenska kennelklubbens format.")
        .build(),
);

const errorMap = {
    415: new UploadError("SBRV.RW.bilaga.felTypRubrikLabel", "SBRV.RW.bilaga.felTypTextLabel"),
    413: new UploadError("SBRV.RW.bilaga.felStorlekRubrikLabel", "SBRV.RW.bilaga.felStorlekTextLabel"),
    422: new UploadError("SBRV.RW.bilaga.felPaFilRubrikLabel", "SBRV.RW.bilaga.felPaFilTextLabel"),
    404: new UploadError("SBRV.RW.bilaga.felTeknisktRubrikLabel", "SBRV.RW.bilaga.felTeknisktTextLabel"),
    500: new UploadError("SBRV.RW.bilaga.felTeknisktRubrikLabel", "SBRV.RW.bilaga.felTeknisktTextLabel"),
};

export default Vue.extend({
    name: "HundensBedrifter",
    components: {
        FFormStep,
        FFormStepButton,
        FRadioGroup,
        FRadioGroupField,
        EFileUpload,
        FkRichtext,
        FSelectField,
        FTextareaField,
        FTextField,
        FTooltip,
    },
    data() {
        return {
            errorMap,
            bastaPlacering: null,
            ovrigaPlaceringar: null,
            beskriv: null,
            harTavlat: null,
            kopplaDiplom: null,
            diplom: null,
            idMarkt: null,
            vemIdMarkt: null,
            idnummer: "",
            namnIdMarkare: "",
            normalModel: null,
            diplomFiler: [],
        };
    },
});
