






















import Vue, { PropOptions } from "vue";
import { FCheckboxGroup, FCheckboxGroupField } from "@fkui/vue";
import { BarnModel } from "@/services";

export default Vue.extend({
    components: { FCheckboxGroup, FCheckboxGroupField },
    model: {
        prop: "model",
        event: "change",
    },
    props: {
        model: {
            type: Array,
            required: true,
        } as PropOptions<Array<string>>,
        valtBarn: {
            type: Object,
            required: true,
        } as PropOptions<BarnModel>,
    },
    data() {
        return {
            sjukdomar: [...this.model],
            sjukdomslista: [
                {
                    id: "feber",
                    namn: "Feber",
                },
                {
                    id: "forkylning",
                    namn: "Förkylning",
                },
                {
                    id: "halsfluss",
                    namn: "Halsfluss",
                },
                {
                    id: "huvudvark",
                    namn: "Huvudärk eller migrän",
                },
                {
                    id: "influensa",
                    namn: "Influensa",
                },
                {
                    id: "kraksjuka",
                    namn: "Kräksjuka eller vinterkräksjuka",
                },
                {
                    id: "magsjukdom",
                    namn: "Magsjukdom",
                },
                {
                    id: "annansjukdom",
                    namn: "Annan sjukdom",
                },
            ],
        };
    },
    methods: {
        onChange(): void {
            this.$emit("change", this.sjukdomar);
        },
    },
});
