

































































































import Vue, { PropOptions } from "vue";
import moment from "moment";
import { ValidationService } from "@fkui/logic";
import {
    FModal,
    FValidationGroup,
    FErrorList,
    FNumericTextField,
    FRadioGroup,
    FRadioGroupField,
    GroupValidityEvent,
    ErrorItem,
    ComponentValidityEvent,
} from "@fkui/vue";
import { DagMedVab } from "@/services/ApplicationModel/ApplicationModelInterface";

moment.updateLocale("sv", {
    week: {
        dow: 1, // Monday is the first day of the week.
        doy: 4, // Used to determine first week of the year.
    },
});

export default Vue.extend({
    components: {
        FModal,
        FValidationGroup,
        FErrorList,
        FNumericTextField,
        FRadioGroup,
        FRadioGroupField,
    },
    props: {
        save: {
            type: Function,
            required: true,
        },
        cancel: {
            type: Function,
            required: true,
        },
        selectedDate: {
            type: String,
            required: true,
        },
        days: {
            type: Object,
            required: true,
        } as PropOptions<Record<string, DagMedVab>>,
    },
    data() {
        return {
            edit: {} as DagMedVab,
            validity: { isValid: true, componentsWithError: [], componentCount: 0 } as GroupValidityEvent,
        };
    },
    computed: {
        prettyDate(): string {
            const formattedDate = moment(this.selectedDate).format("dddd D MMMM YYYY");
            return formattedDate[0].toUpperCase() + formattedDate.slice(1);
        },
        errors(): ErrorItem[] {
            return this.validity.componentsWithError.map((c: ComponentValidityEvent) => ({
                id: c.elementId,
                focusElementId: c.focusElementId,
                title: c.errorMessage,
            }));
        },
    },
    async mounted(): Promise<void> {
        if (this.editedDay()) {
            this.$set(this, "edit", { ...this.editedDay() });
            await this.$nextTick(); // wait for model updates before revalidation
            const group = this.$refs.group as Vue;
            await ValidationService.validateAllElements(group.$el.id);
        }
    },
    methods: {
        async onSave(): Promise<void> {
            const group = this.$refs.group as Vue;
            ValidationService.setState(group.$el, { submitted: true });
            await ValidationService.validateAllElements(group.$el.id);
            await this.$nextTick();
            if (this.validity.isValid) {
                this.$set(this.days, this.selectedDate, { ...this.edit });
                this.save();
            } else {
                console.log("todo focus");
                // focus(this.$refs.errors as HTMLElement);
            }
        },
        onChangeOmfattning() {
            if (this.edit.omfattning !== "ange") {
                this.edit.vabbatTimmar = "";
                this.edit.vabbatMinuter = "";
            }
        },
        editedDay(): DagMedVab {
            return this.days[this.selectedDate];
        },
    },
});
