






















import Vue, { PropOptions } from "vue";
import { DagMedVab } from "@/services/ApplicationModel/ApplicationModelInterface";
import VabEditBox from "./VabEditBox.vue";
import VabDaySummary from "./VabDaySummary.vue";
import { ECalendar, CalendarState, ECalendarComponentOptions, DayInterface } from "@exp/vue";

export default Vue.extend({
    components: {
        ECalendar,
        VabEditBox,
        VabDaySummary,
    },
    model: {
        prop: "model",
        event: "change",
    },
    props: {
        model: {
            type: Object,
            required: true,
        } as PropOptions<Record<string, DagMedVab>>,
    },
    data() {
        return {
            calendarState: new CalendarState(),
            calendarOptions: new ECalendarComponentOptions({
                showWeekNumbers: true,
                showPlanning: false,
            }),
            internalModel: {} as Record<string, DagMedVab>,
            isOpen: true,
            omfattning: null,
        };
    },
    watch: {
        model: {
            immediate: true,
            deep: true,
            handler() {
                this.$set(this, "internalModel", this.model);

                const days: Record<string, DayInterface> = {};
                Object.keys(this.internalModel).forEach(
                    (day) => (days[day] = { selected: false, added: true, collectedData: [] }),
                );
                this.calendarState = new CalendarState(days);
            },
        },
    },
    methods: {
        onDayRemoved(day: string): void {
            this.$delete(this.internalModel, day);
            this.$emit("change", this.internalModel);
        },
    },
});
