









































import Vue, { PropOptions } from "vue";
import { EArbetsgivareModule, EArbetsgivareModuleData, EArbetsgivareModuleOptions } from "@exp/arbetsgivare-module";
import { FModal, FForm, FIcon } from "@fkui/vue";

export default Vue.extend({
    name: "ArbetsgivareWizardModule",
    components: { FModal, EArbetsgivareModule, FForm, FIcon },
    model: {
        event: "change",
        prop: "value",
    },
    props: {
        value: {
            required: true,
            type: Object,
        } as PropOptions<EArbetsgivareModuleData>,
    },
    data() {
        return {
            data: new EArbetsgivareModuleData([...this.value.arbetsgivareList]),
            options: new EArbetsgivareModuleOptions({
                isEdit: false,
                redigeraNarmasteChefUppgifter: false,
                frivilligNarmasteChefUppgifter: true,
            }),
        };
    },
    watch: {
        value: function () {
            this.data = new EArbetsgivareModuleData([...this.value.arbetsgivareList]);
        },
    },
    methods: {
        onSubmit(): void {
            this.options.isEdit = false;
            this.$emit("change", this.data);
        },
        onAbort(): void {
            this.data = new EArbetsgivareModuleData([...this.value.arbetsgivareList]);
            this.options.isEdit = false;
        },
    },
});
