









































import Vue, { PropOptions } from "vue";
import { ESgiModule, ESgiModuleData, ESgiModuleOptions } from "@exp/sgi-module";
import { FModal, FForm, FIcon } from "@fkui/vue";

export default Vue.extend({
    name: "SgiWizardModule",
    components: { ESgiModule, FModal, FForm, FIcon },
    model: {
        event: "change",
        prop: "value",
    },
    props: {
        value: {
            required: true,
            type: Object,
        } as PropOptions<ESgiModuleData>,
    },
    data() {
        return {
            data: new ESgiModuleData({ ...this.value.inkomstuppgifter }),
            options: new ESgiModuleOptions({
                isEdit: false,
                showArsinkomstFranAnstallning: true,
                showArsinkomstEgenVerksamhet: false,
                editArsarbetstidOforandradLon: false,
            }),
        };
    },
    watch: {
        value: function () {
            this.data = new ESgiModuleData({ ...this.value.inkomstuppgifter });
        },
    },
    methods: {
        onSubmit(): void {
            this.options.isEdit = false;
            this.$emit("change", this.data);
        },
        onAbort(): void {
            this.data = new ESgiModuleData({ ...this.value.inkomstuppgifter });
            this.options.isEdit = false;
        },
    },
});
