






import Vue from "vue";
import { FLoader } from "@fkui/vue";
import { AnsokanService } from "@/services/AnsokanService";
import { SigningAbortedError, TimeoutError, UnavailableError } from "@exp/logic";

export default Vue.extend({
    name: "Finalize",
    components: {
        FLoader,
    },
    async mounted(): Promise<void> {
        try {
            this.preventBrowserBack();
            await AnsokanService.finalizeSigning();
            this.$router.push({
                name: "Kvittens",
            });
        } catch (err) {
            this.handleError(err);
        }
    },
    methods: {
        rerouteToAnsokan() {
            AnsokanService.fetchModelFromSessionStorage();
            AnsokanService.removeModelFromSessionStorage();
            this.$router.push({
                name: "Ansökan",
            });
        },
        preventBrowserBack() {
            window.history.pushState(null, "", window.location.href);
            window.onpopstate = () => {
                history.go(1);
            };
        },
        handleError(error: Error) {
            /**
             * @todo visa olika felmeddelande beroende på vilket fel som triggas
             */
            switch (error.constructor) {
                case UnavailableError:
                    this.rerouteToAnsokan();
                    break;
                case TimeoutError:
                    this.rerouteToAnsokan();
                    break;
                case SigningAbortedError:
                    this.rerouteToAnsokan();
                    break;
                default:
                    throw error;
            }
        },
    },
});
