







































































import Vue from "vue";
import { FFormStep, FFormStepButton } from "@fkui/vue";
import { EKontaktModuleData, EKontaktModuleOptions, KontaktModuleService, EKontaktModule } from "@exp/kontakt-module";
import { EKontoModuleData, EKontoModuleOptions, KontoService, EKontoModule } from "@exp/konto-module";
import {
    EArbetsgivareModuleOptions,
    EArbetsgivareModuleData,
    ArbetsgivareService,
    EArbetsgivareModule,
} from "@exp/arbetsgivare-module";
import { ESgiModuleData, ESgiModuleOptions, SgiService, ESgiModule } from "@exp/sgi-module";
import { AnsokanService } from "@/services";

export default Vue.extend({
    name: "MinaUppgifter",
    components: {
        EKontaktModule,
        EKontoModule,
        EArbetsgivareModule,
        ESgiModule,
        FFormStep,
        FFormStepButton,
    },
    data() {
        return {
            arbetsgivarData: new EArbetsgivareModuleData(),
            arbetsgivareModuleOptions: new EArbetsgivareModuleOptions({
                isEdit: true,
                redigeraNarmasteChefUppgifter: true,
                frivilligNarmasteChefUppgifter: false,
            }),
            kontaktData: new EKontaktModuleData(),
            kontaktOptions: new EKontaktModuleOptions(false),
            kontoData: new EKontoModuleData(),
            kontoOptions: new EKontoModuleOptions(false),
            hasBeenOpened: false,
            showAbortModal: false,
            sgiData: new ESgiModuleData(),
            sgiOptions: new ESgiModuleOptions({
                isEdit: true,
                showArsinkomstFranAnstallning: true,
                showArsinkomstEgenVerksamhet: true,
                editArsarbetstidOforandradLon: true,
            }),
        };
    },
    async created(): Promise<void> {
        const arbetsgivarlista = await ArbetsgivareService.getArbetsgivarlista();
        this.arbetsgivarData = new EArbetsgivareModuleData(arbetsgivarlista);

        const inkomstuppgifter = await SgiService.getInkomstuppgifter();
        this.sgiData = new ESgiModuleData(inkomstuppgifter);

        const konto = await KontoService.getKonto();
        this.kontoData = new EKontoModuleData(konto);

        const kontakt = await KontaktModuleService.getKontaktUppgifter();
        this.kontaktData = new EKontaktModuleData(kontakt);

        this.$emit("loading-finished");
    },
    methods: {
        updateArbetsgivareModuleOptions(isOpen: boolean): EArbetsgivareModuleOptions {
            this.arbetsgivareModuleOptions.isEdit = isOpen || this.hasBeenOpened === false;
            return this.arbetsgivareModuleOptions;
        },
        updateSgiModuleOptions(isOpen: boolean): ESgiModuleOptions {
            this.sgiOptions.isEdit = isOpen || this.hasBeenOpened === false;
            return this.sgiOptions;
        },
        updateKontaktModuleOptions(isOpen: boolean): EKontaktModuleOptions {
            this.kontaktOptions.isEdit = isOpen;
            return this.kontaktOptions;
        },
        updateKontoModuleOptions(isOpen: boolean): EKontoModuleOptions {
            this.kontoOptions.isEdit = isOpen;
            return this.kontoOptions;
        },
        setKontaktData(kontaktData: EKontaktModuleData) {
            AnsokanService.setKontaktData(kontaktData);
        },
        setKontoData(kontoData: EKontoModuleData) {
            AnsokanService.setKontoData(kontoData);
        },
        setSgiData(sgiData: ESgiModuleData) {
            AnsokanService.setSgiData(sgiData);
        },
        setArbetsgivarData(arbetsgivarData: EArbetsgivareModuleData) {
            AnsokanService.setArbetsgivarData(arbetsgivarData);
        },
    },
});
