import {
    HundControllerApi,
    HundarRESTVO,
    AnsokanRESTVO,
    InitSigneraVO,
    FelmeddelandeRestVO,
    Configuration,
} from "hund-api/dist/typescript-axios";
import {
    RestServiceInterface,
    KvittensResponse,
    BarnlistaRESTVO,
} from "./RestServiceInterface";
import dayjs from "dayjs";
import { errorHandlingProxy } from "@exp/logic";

export class RestServiceImpl implements RestServiceInterface {
    private KANAL = 1;
    private HUNDREST_BASE_PATH = "/fk_apps/HUNDREST";
    private api: HundControllerApi;

    public constructor() {
        this.api = errorHandlingProxy<HundControllerApi, FelmeddelandeRestVO>(
            new HundControllerApi(
                new Configuration({
                    basePath: this.HUNDREST_BASE_PATH,
                }),
            ),
        );
    }

    public async getBarn(): Promise<BarnlistaRESTVO> {
        return Promise.resolve({
            barnlista: [
                {
                    fornamn: "Pascal",
                    efternamn: "Exempelsson",
                    pnr: "20180107-1234",
                    fodelsedatum: "2018-01-07",
                },
                {
                    fornamn: "Wilma",
                    efternamn: "Exempelsson",
                    pnr: "20141228-1234",
                    fodelsedatum: "2014-12-28",
                },
            ],
        });
    }

    public async getHundar(): Promise<HundarRESTVO> {
        const response = await this.api.hamtaHundar(this.KANAL);
        return response.data;
    }

    public async postAnsokan(ansokan: AnsokanRESTVO): Promise<InitSigneraVO> {
        const response = await this.api.ansokan(this.KANAL, ansokan);
        return response.data;
    }

    public async postSignera(): Promise<KvittensResponse> {
        const response = await this.api.avslutaAnsokan(this.KANAL);
        const datum = dayjs(response.headers.date).format("YYYY-MM-DD");
        return {
            datum,
            ...response.data,
        };
    }
}

export const RestService = new RestServiceImpl();
