









































import Vue, { PropOptions } from "vue";
import { EKontaktModule, EKontaktModuleData, EKontaktModuleOptions } from "@exp/kontakt-module";
import { FModal, FForm, FIcon } from "@fkui/vue";

export default Vue.extend({
    name: "KontaktWizardModule",
    components: { EKontaktModule, FModal, FForm, FIcon },
    model: {
        event: "change",
        prop: "value",
    },
    props: {
        value: {
            required: true,
            type: Object,
        } as PropOptions<EKontaktModuleData>,
    },
    data() {
        return {
            data: new EKontaktModuleData({ ...this.value }),
            options: new EKontaktModuleOptions(false),
        };
    },
    watch: {
        value: function () {
            this.data = new EKontaktModuleData({ ...this.value });
        },
    },
    methods: {
        onSubmit(): void {
            this.options.isEdit = false;
            this.$emit("change", this.data);
        },
        onAbort(): void {
            this.data = new EKontaktModuleData({ ...this.value });
            this.options.isEdit = false;
        },
    },
});
