


















import { AnsokanService } from "@/services";
import { ArbetsgivareService, EArbetsgivareModuleData } from "@exp/arbetsgivare-module";
import { EKontaktModuleData, KontaktModuleService } from "@exp/kontakt-module";
import { EKontoModuleData, KontoService } from "@exp/konto-module";
import { ESgiModuleData, SgiService } from "@exp/sgi-module";
import Vue from "vue";
import { SgiWizardModule, ArbetsgivareWizardModule, KontoWizardModule, KontaktWizardModule } from ".";

export default Vue.extend({
    name: "DinaUppgifter",
    components: { SgiWizardModule, ArbetsgivareWizardModule, KontoWizardModule, KontaktWizardModule },
    data() {
        return { ansokanModel: AnsokanService.getModel() };
    },
    async beforeMount() {
        this.ansokanModel.sgiData = new ESgiModuleData(await SgiService.getInkomstuppgifter());
        this.ansokanModel.arbetsgivarData = new EArbetsgivareModuleData(
            await ArbetsgivareService.getArbetsgivarlista(),
        );
        this.ansokanModel.kontaktData = new EKontaktModuleData(await KontaktModuleService.getKontaktUppgifter());
        this.ansokanModel.kontoData = new EKontoModuleData(await KontoService.getKonto());
    },
});
