







import { DagMedVab } from "@/services/ApplicationModel/ApplicationModelInterface";
import Vue, { PropOptions } from "vue";
export default Vue.extend({
    props: {
        day: {
            type: Object,
            required: true,
        } as PropOptions<DagMedVab>,
    },
    computed: {
        omfattning(): string {
            if (this.day.omfattning === "hela") {
                return "100";
            } else if (this.day.omfattning === "halva") {
                return "50";
            } else {
                return this.beraknaOmfattning(this.day);
            }
        },
    },
    methods: {
        beraknaOmfattning(dag: DagMedVab): string {
            const jobbatTotalaMinuter = this.totalaMinuter(dag.jobbatTimmar, dag.jobbatMinuter);
            const vabbatTotalaMinuter = this.totalaMinuter(dag.vabbatTimmar, dag.vabbatMinuter);
            const kvot = vabbatTotalaMinuter / jobbatTotalaMinuter;
            const fastaProcent = [1, 0.75, 0.5, 0.25, 0.125];
            const diffar = fastaProcent.map((fastProcent) => this.diff(fastProcent, kvot));
            const narmasteFastProcent = fastaProcent[diffar.indexOf(Math.min(...diffar))];

            return `${narmasteFastProcent * 100}`.replace(".", ",");
        },
        totalaMinuter(timmar: number | "", minuter: number | ""): number {
            return Number(timmar) * 60 + Number(minuter); // Number("") = 0
        },
        diff(fastProcent: number, kvot: number): number {
            return fastProcent > kvot ? fastProcent - kvot : kvot - fastProcent;
        },
    },
});
