import {
    BarnModel,
    ApplicationModel,
    KvittensModel,
} from "@/services/ApplicationModel";
import { AnsokanRESTVO, SigneringRestVO } from "hund-api/dist/typescript-axios";
import { ROUTE_AVSLUTASIGNERING } from "@/constants";
import { KvittensResponse } from "@/repository/RestService";
import { BarnlistaRESTVO } from "@/repository/RestService/RestServiceInterface";

function getSigneringRestVO(): SigneringRestVO {
    return {
        returnUrl: `${
            window.location.href.split("#")[0]
        }#${ROUTE_AVSLUTASIGNERING}`,
    };
}

export function fromRESTtoBarnModel({
    barnlista,
}: BarnlistaRESTVO): BarnModel[] {
    return barnlista.reduce((acc, current) => {
        acc.push({ ...current, genetiv: `${current.fornamn}s` });
        return acc;
    }, [] as BarnModel[]);
}

export function fromModelToAnsokanREST(model: ApplicationModel): AnsokanRESTVO {
    return {
        bidragsDagar: [],
        kontaktuppgifter: { telefonnummer: model.kontaktData.telefon1 },
        hunduppgifter: {
            namn: model.valtBarn.fornamn,
            fodelsear: Number(model.valtBarn.fodelsedatum),
        },
        signering: getSigneringRestVO(),
    };
}

export function fromRESTtoKvittensModel(
    kvittensResponse: KvittensResponse,
): KvittensModel {
    const {
        namn,
        fodelsear,
        datum,
        informationsmeddelanden,
    } = kvittensResponse;
    return {
        namn: namn ? namn : "",
        fodelsear: fodelsear ? String(fodelsear) : "",
        datum,
        informationsmeddelanden,
    };
}
