import "./polyfills";

import App from "@/App.vue";
import Axios from "axios";
import LoadingApp from "@/LoadingApp.vue";
import router from "@/router";
import Router from "vue-router";
import {
    ClientLog,
    translateValidationMessages,
    ETranslationProvider,
    ETechnicalError,
} from "@exp/vue";
import { TranslationService } from "@fkui/logic";
import {
    ValidationPlugin,
    FErrorHandlingApp,
    TestPlugin,
    TranslationPlugin,
    EventBus,
    ErrorPlugin,
    ErrorData,
    UNHANDLED_ERROR_EVENT,
} from "@fkui/vue";
import Vue, { VNode } from "vue";
import "./main.scss";
import { APP_TRANSLATIONS_URL, APP_SLUG } from "@/constants";
import "fk-icons/dist/f";
import { AnsokanService } from "@/services";

Vue.config.productionTip = false;
Axios.defaults.headers.common["X-Requested-With"] = APP_SLUG;

Vue.use(Router);
Vue.use(ValidationPlugin);
Vue.use(TestPlugin);

async function createLoadingApp(): Promise<void> {
    new Vue({
        render: (h): VNode =>
            h(FErrorHandlingApp, {
                props: {
                    defaultComponent: LoadingApp,
                    errorComponent: ETechnicalError,
                },
            }),
    }).$mount("#app");
}

function showTranslationKeysBasedOnCookie(): void {
    if (document.cookie.indexOf("disable-translate=true") > -1) {
        TranslationService.provider.changeLanguage("cimode");
    }
}

async function loadTranslations(): Promise<void> {
    const translationProvider = new ETranslationProvider();
    const translateFunction = await translationProvider.createTranslateFunction(
        { language: "sv" },
    );

    try {
        await translationProvider.loadTranslations(APP_TRANSLATIONS_URL);
    } catch (error) {
        // Ignore error silently.
    } finally {
        TranslationService.changeProvider(translationProvider);

        if (process.env.NODE_ENV === "development") {
            showTranslationKeysBasedOnCookie();
        }

        Vue.use(TranslationPlugin);
    }

    translateValidationMessages(translateFunction);
}

async function loadInitialData(): Promise<void> {
    await AnsokanService.initialize();
}

async function createApp(): Promise<void> {
    new Vue({
        router,
        render: (h): VNode =>
            h(FErrorHandlingApp, {
                props: {
                    defaultComponent: App,
                    errorComponent: ETechnicalError,
                },
            }),
    }).$mount("#app");
}

createLoadingApp()
    .then(loadTranslations)
    .then(loadInitialData)
    .then(createApp)
    .catch((error) => {
        EventBus.$emit(
            UNHANDLED_ERROR_EVENT,
            new ErrorData(error, (null as unknown) as Vue, ""),
        );
    });
