









import Vue, { PropOptions } from "vue";
import { FRadioGroup, FRadioGroupField } from "@fkui/vue";
import { BarnModel } from "@/services/ApplicationModel/ApplicationModelInterface";
export default Vue.extend({
    name: "BarnValjare",
    components: {
        FRadioGroup,
        FRadioGroupField,
    },
    model: {
        prop: "valtBarn",
        event: "change",
    },
    props: {
        valtBarn: {
            type: Object,
            required: true,
        } as PropOptions<BarnModel>,
        barn: {
            type: Array,
            required: true,
        } as PropOptions<BarnModel[]>,
    },
    computed: {
        valtBarnModel: {
            get(): BarnModel {
                return this.valtBarn;
            },
            set(valtBarn: BarnModel): void {
                this.$emit("change", valtBarn);
            },
        },
    },
});
